<template>
  <div class="container">
    <div class="login" v-if="show !== 'login'">
      <div class="login-title">登陆</div>
      <div class="login-item username">
        <i class="iconfont icon-yonghuming"></i>
        <input type="text" placeholder="请输入用户名" ref="username" @blur.prevent="checkUser">
        <span class="error">{{userInfoTip}}</span>
      </div>
      <div class="login-item password">
        <i class="iconfont icon-mima"></i>
        <input type="password" placeholder="请输入密码" ref="password" @blur.prevent="checkPass">
        <span class="error">{{passInfoTip}}</span>
      </div>
      <div class="login-item submit" @click="login">
        <input type="button" class="login-btn" value="登 陆">
      </div>
    </div>
    <diary v-if="show==='login'"></diary>
  </div>
</template>

<script>
import Diary from '@/pages/diary'

export default {
  components: {
    Diary
  },
  data () {
    return {
      isLogin: '',
      userInfoTip: '',
      passInfoTip: ''
    }
  },
  computed: {
    show () {
      return this.$store.state.login
    }
  },
  methods: {
    checkUser () {
      let username = this.$refs.username.value
      if (username === '') {
        this.userInfoTip = '用户名不能为空'
      } else if (username !== 'pxy') {
        this.userInfoTip = '用户名不存在'
      } else {
        this.userInfoTip = ''
      }
    },
    checkPass () {
      let password = this.$refs.password.value
      if (password === '') {
        this.passInfoTip = '密码不能为空'
      } else if (password !== 'pxy1314..') {
        this.passInfoTip = '密码错误'
      } else {
        this.passInfoTip = ''
      }
    },
    login () {
      let username = this.$refs.username.value
      let password = this.$refs.password.value
      if (username === 'pxy' && password === 'pxy1314..') {
        this.isLogin = 'login'
        this.$store.commit('saveLogin', this.isLogin)
      } else if (username === '' && password === '') {
        this.userInfoTip = '用户名不能为空'
        this.passInfoTip = '密码不能为空'
      } else if (username === '') {
        this.userInfoTip = '用户名不能为空'
      } else if (password === '') {
        this.passInfoTip = '密码不能为空'
      } else if (username !== 'pxy') {
        this.userInfoTip = '用户名不存在'
      } else if (password !== 'pxy1314..') {
        this.passInfoTip = '密码错误'
      }
      let timer
      clearTimeout(timer)
      timer = setTimeout(() => {
        localStorage.clear()
      }, 10000)
    }
  }
}
</script>

<style scoped>
.container {
  background-color: #eef7ef;
  position: relative;
  display: flex;
  min-height: 100vh;
}
.login {
  box-shadow:#abe8db 0 0 2rem;
  width: 8rem;
  height: 6.8rem;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.login-title{
  text-align: center;
  font-size: .48rem;
  padding: .3rem;
  margin-bottom: 1rem;
  color: #abe8db;
  border-bottom: .04rem solid #abe8db;
}
.login-item {
  position: relative;
  height: .84rem;
  margin: .6rem;
  border: .02rem solid #dedede;
  border-radius: .08rem;
}
.submit {
  border: .02rem solid #eef7ef;
  cursor: pointer;
}
.login-item>input {
  width: calc(100% - .7rem);
  height: 100%;
}
.login-item>i {
  font-size: .42rem;
  color: #bcbcbc;
  padding: 0 .1rem;
  vertical-align: middle;
}
.error {
  display: inline-block;
  padding-top: .2rem;
  padding-left: .6rem;
  font-size: .24rem;
  color: #dd0505;
  width: 100%;
}
.login-item>.login-btn {
  width: 100%;
  background-color: #eef7ef;
  color: #999;
  font-size: .32rem;
  font-weight: bold;
}
.login-btn:hover {
  background-color: #ddf3ea;
  color: #aaa;
}
@media screen and (max-width: 400px) {
  .login {
    width: 95%;
  }
}
</style>



