export const diaryItem = {
  data () {
    return {
      text: ''
    }
  },
  mounted () {
    let h = this.$refs.box.offsetHeight
    if (h >= 125) {
      this.text = '展开全文'
    } else {
      this.text = ''
    }
  },
  methods: {
    handleItem () {
      if (this.text === '展开全文') {
        this.$refs.box.style.maxHeight = '100%'
        this.text = '收起'
      } else {
        this.$refs.box.style.maxHeight = '125px'
        this.text = '展开全文'
      }
    }
  }
}



