<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/23 21:05</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      今天，无意间打开了备忘录，看到上面一个个时间段，最后时间停留在2018年9月30，可能后面也不会再去记录吧。不知道你有没有想我，不知道你有没有像我那样有很想找对方的冲动，不知道你过得怎么样。看不到你的动态，只能想象着你过着怎样的生活。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
    <div class="item-img-1"><img src="../../assets/images/2018102301.jpg" alt=""></div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



