<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/22 01:09</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      这几天，做事完全不在状态，关闭电视只关闭机顶盒却不把显示器关了。为了把菜放进冰箱那盘把菜装起来后然后就离开了。感觉自己老了好多，本身不爱讲话的我一天除了吃饭感觉都没张什么嘴了。我很想找你，我很想问你过得怎么样，我也很想知道你和他发展怎么样，我想知道你的一切，可是我又要装着不在乎，装着自己过得还OK。然而，自己那完全不是过日子，而是在熬日子。我时不时看空间，看有没有你的动态，发现每次打开，一无所有，每一次的打开，只会是每一次的失望。我看到前几天你还会来我空间看看，而如今，我的空间已经没有了你的足迹，或许我就会这样被逐渐遗忘吧。今晚我故意很晚上床，我不想那么早上去难熬，我不停的敲打着键盘，不让自己去想，可是我发现这只是假象。漫无目的地去做，完全没有收获，只是让自己身体上看着很忙，而心思完全不在。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



