<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/18 22:00</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      今天，你给我说你在考虑答应别人，其实在之前，你也提到过，但是我都说不信,因为我不相信你会这么快答应别人，也不会这么快就真的选择放弃这一段感情。可是今天，我能从你的话听出那份认真，所以当你说完我再三确认，得知你的想法后，我也是选择了放弃通话。挂断电话，心理是很不好受的，再加上这突如其来的感冒，真的是雪上加霜。感觉身上，心里好冷，不知道是因为感冒还是因为知道这个难以接受的结果。这份伤心，似乎比以前的吵架更伤心，因为我知道，你在考虑别人的时候可能真的打算放弃我了。我一直以为我还是那个我，还是那个可以全心全意对你好的我，然而，你觉得我没那么在乎你了，这是其一，其二，你总觉得我的父母不欢迎你，其实并没有，我跟他们一起生活了二十多年，彼此都是相当熟悉相当了解的，他们喜欢和不喜欢一个人我肯定是知道的。我也给你解释了很多次，然而你还是不相信。我也知道，其实最主要的原因还是你觉得我的态度，每次你生气了我不是立马来哄你，而是选择过一会或者隔天才来找你。我也想第一时间找你，可是当你在生气的时候我可能也在气，那个时间不能完全的冷静下来，因为很多次你生气的都是因为不起眼的事，这样当你生气时，我第一时间想的时，这算什么事，有必要生气么，这样也会让我莫名的生气起来。而当我冷静下来找你的时候可能因为隔了一些时间，就让你觉得我态度不好，没有第一时间来解释或者什么，这或许需要成长吧。
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



