<template>
  <div class="diary">
    <div class="top">分开的第{{time}}</div>
    <div class="diary-container">
      <diary1></diary1>
      <diary2></diary2>
      <diary3></diary3>
      <diary4></diary4>
      <diary5></diary5>
      <diary6></diary6>
      <diary7></diary7>
    </div>
  </div>
</template>

<script>
import Diary1 from 'components/20181018'
import Diary2 from 'components/20181019'
import Diary3 from 'components/20181020'
import Diary4 from 'components/20181021'
import Diary5 from 'components/20181022'
import Diary6 from 'components/20181023'
import Diary7 from 'components/20181024'
export default {
  components: {
    Diary1,
    Diary2,
    Diary3,
    Diary4,
    Diary5,
    Diary6,
    Diary7
  },
  data () {
    return {
      time: ''
    }
  },
  created () {
    let that = this
    setInterval(() => {
      let date = new Date()
      let startDate = new Date('2018-10-18')
      let hours = date.getHours()
      let minutes = date.getMinutes()
      let seconds = date.getSeconds()
      let leftTime = date.getTime() - startDate.getTime()
      let leftSecond = parseInt(leftTime / 1000)
      let leftDay = Math.floor(leftSecond / (3600 * 24))
      that.time = `${leftDay}天${hours}小时${minutes}分${seconds}秒`
    }, 1000)
  }
}
</script>

<style scoped>
.diary {
  width: 16rem;
  margin: 0 auto;
  background-color: #2e2e2e;
  color: #a6a6a6;
  padding-bottom: .4rem;
}
.top {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: .8rem;
  text-align: center;
  line-height: .8rem;
  background-color: #eef7ef;
  color: #38a4ab;
  font-size: .32rem;
  font-weight: bold;
}
.diary-container {
  margin-top: .8rem;
}
</style>



