<template>
  <div class="item">
    <div class="item-title">
      <span class="img"><img src="../../assets/images/user.png" alt=""></span>
      <div>
        <span class="title">致逝去的青春</span>
        <span class="time">2018/10/19 23:12</span>
      </div>
    </div>
    <div class="item-detail" ref="box">
      昨晚，是一个非常漫长的夜晚。感冒有点厉害，可能是鼻子堵塞的原因，有点呼吸困难，盖了两床铺盖，却感觉还是手冷，脚冷，背心冷，不过这些冷都抵挡不了心里的冷。我发了一条说说，不过屏蔽了你，这是我第一次发说说还选择屏蔽人吧。之所以不想让你看到，是因为不想让你看到如此可怜的我。说说中虽然说的是感冒难受，可是我想说的是难受不是因为感冒，而是因为可能就此失去一段感情心里难受。我想了很多很多，可是更多的不舍和想念...
    </div>
    <div class="more" @click="handleItem">{{text}}</div>
  </div>
</template>

<script>
import {diaryItem} from '@/assets/js/mixin'

export default {
  mixins: [diaryItem]
}
</script>

<style scoped>
  @import '../../assets/styles/main.css'
</style>



